@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0A0E17;
  color: white;
  overflow: hidden;
  touch-action: manipulation;
}

/* Accesibilidad: Aumentar área de toque */
button, 
a {
  min-height: 44px;
  min-width: 44px;
}

/* Eliminar contorno azul en elementos táctiles en Safari */
* {
  -webkit-tap-highlight-color: transparent;
}

/* Animaciones planetarias */
.orbit {
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

.planet {
  position: absolute;
  transform-origin: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.planet:hover, .planet:focus {
  transform: scale(1.2);
}

/* Accesibilidad: Indicador de foco visible */
:focus {
  outline: 3px solid #4F6DE8;
  outline-offset: 2px;
}

/* Accesibilidad: Preferencia de movimiento reducido */
@media (prefers-reduced-motion: reduce) {
  .orbit-animation {
    animation-duration: 0s !important;
    transition-duration: 0s !important;
  }
}

/* Optimización para iPad */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  button, a {
    min-height: 60px;
    min-width: 60px;
  }
}
